<template>
  <div class="page CoverSpecial page-break-after">
    <div class="logo-container">
      <div class="logo"></div>
      <div class="logo-info ">详细了解企业信用尽在百融征信</div>
    </div>
    <div class="clear"></div>
    <div class="title-container title-container-position">
      <div class="title">
        企业信用报告 - 专业版
      </div>
      <div class="sub-title">
        本报告生成时间: {{ nowDate }}
      </div>
      <div class="sub-title">
        您所看到的报告内容为截止至该时间点该公司的百融征信数据快照
      </div>
    </div>
		
  </div>
</template>
<script>
export default {
  name: "CoverSpecial",
  data() {
    return {
      nowDate: ''
    };
  },
  created () {
    let date = new Date()

    let month = date.getMonth() + 1
    month = month > 10 ? month : '0' + month

    let day = date.getDate()
    day = day > 10 ? day : '0' + day

    let hour = date.getHours()
    hour = hour > 10 ? hour : '0' + hour

    let min = date.getMinutes() 
    min = min > 10 ? min : '0' + min

    let second = date.getSeconds()
    second = second > 10 ? second : '0' + second

    this.nowDate = date.getFullYear() + '.' + month + '.' + day
      + ' ' + hour + ':' + min + ':' + second

  }
};
</script>
<style lang="scss" scoped>
.background-module {
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.clear {
  clear: both;
}
.CoverSpecial {
  background-image: url('../../assets/creditpaper/cover-special-background.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.logo-container {
  float: right;
  margin-top: 60px;
  margin-right: 30px;
}
.logo {
  width: 233px;
  height: 59px;
  @extend .background-module;
  background-image: url('../../assets/creditpaper/cover-special-logo.png');
 
}
.logo-info {
  width: 237px;
  font-size: 29px;
  line-height: 41px;
  text-align: right;
  color: #999999;
  opacity: .5;
  margin-top: 20px;
}
.title-container-position {
  position: relative;
  top: 214px;
}

.title-container {
  text-align: left;
  width: 620.6px;
  height: 140px;
  margin-top: 14px;
  padding: 20px 0 20px 30px;
  color: #ffffff;
  // @extend .background-module;
  // background-image: url('../../assets/creditpaper/cover-special-title-background.png');
}
.title {
  font-size: 45px;
  line-height: 78px;
}
.sub-title {
  font-size: 20px;
  line-height: 28px;
}
.title + .sub-title {
  margin-top: 15px;
}
.sub-title + .sub-title {
  margin-top: 6px;
}

</style>



