<script>
import { mapState } from 'vuex'
import Loading from '../Loading.vue'
export default {
  name: "",
  components: {
    'vLoading': Loading
  },
  data() {
    return {
      
    }
     
  },
  computed: {
    ...mapState({
      basicInfo: state => state.manage.basicInfo,
      personInfoList: state => state.manage.personInfoList,
      filiationInfoList: state => state.manage.filiationInfoList,
      alterInfoList: state => state.manage.alterInfoList,
      bizLicenceList: state => state.manage.bizLicenceList,
      bizAnnualReportList: state => state.manage.bizAnnualReportList,
      Tel: state => state.manage.Tel,
      Email: state => state.manage.Email,
      bizDetailLoading: state => state.manage.bizDetailLoading,
      bizLicneceLoading: state => state.manage.bizLicneceLoading,
      bizAnnualReportListLoading: state => state.manage.bizAnnualReportListLoading


    })
  },
  filters: {
    getPartCode (value) {
      if (!value) {
        return "暂无数据"
      }
      const length = value.length;
      return value.substring(9, length - 1)

    }
  },
  methods : {
  },
  created() {

  }
};
</script>
<style lang="scss" scoped>
@import '../../../static/CSS/variable.scss';
.business-info tr {
  text-align: left;
  font-size: $contentFontSize;
  color: #333333;
  line-height: $contentLineHeight;
  vertical-align: top;

  td:nth-child(2) {
    width: 310px;
  }
  td {
    span:first-child {
      font-weight: bold;
    }
    span:nth-child(2) {
      color: #151515;
    }
  }

  + tr td {
    padding-top: 13px;
  }
}

.member-order {
  width: 210px;
}
.member-name {
  width: 300px;
}
.member-position {
  width: 300px;
}

.organize-name {
