}

.check-order {
  width: 46px;
}
.check-type {
  width: 68px;
}
.check-date {
  width: 84px;
}
.check-result {
  width: 203px;
}
.check-partment {
  width: 134px;
}

.random-order {
  width: 46px;
}
.random-plan-num {
  width: 80px;
}
.random-plan-name {
  width: 68px;
}
.random-mission-num {
  width: 81px;
}
.random-type {
  width: 46px;
}
.random-partment {
  width: 80px;
}
.random-date {
  width: 75px;
}
.random-result {
  width: 59px;
}

.enterprise-order {
  width: 210px;
}
.enterprise-report {
  width: 300px;
}
.enterprise-info {
  width: 300px;
}

</style>






