<template>
  <div class="page-special page-content ">
    <!-- <div class="page-logo-container">
      <div class="page-logo"></div>
    </div> -->
    <div class="chapter-container">
      <div class="chapter-title">
        4 知识产权
      </div>
      <div class="chapter-line"></div>
    </div>
    
    <div  class="module-title">4.1 商标</div>
    <div v-if="bizTrademarkListLoading == 'yes'">
      <v-loading></v-loading>
    </div>
    <div v-else class="module-content">
      <div v-if='bizTrademarkList.length > 0'>
        <table class="logo show-border thead-left">
          <colgroup>
            <col class="logo-order">
            <col class="logo-pic">
            <col class="logo-name">
            <col class="logo-type">
            <col class="logo-status">
          </colgroup>
          <thead>
            <tr>
              <th>序号</th>
              <th>商标图片</th>
              <th>商标名称</th>
              <th>分类</th>
              <th>状态</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(tradeMark, index) in bizTrademarkList" :key="index">
              <td>{{ index + 1 }}</td>
              <!-- <td>{{ tradeMark.Imageurl }}</td> -->
              <td><img class="logo-img" :src="tradeMark.ImageUrl" alt="图标"></td>
              <td>{{ tradeMark.Name | checkVariable }}</td>
              <td>{{ tradeMark.IntCls | checkVariable }}</td>
              <td>{{ tradeMark.Status | checkVariable }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-else class="no-data">
        暂无数据
      </div>
    </div>
    <div class="module-title">4.2 专利</div>
    <div v-if="bizPatentListLoading == 'yes'">
      <v-loading></v-loading>
    </div>
    <div v-else class="module-content">
      <div v-if='bizPatentList.length > 0'>
        <table class="patent show-border thead-left">
          <colgroup>
            <col class="patent-order">
            <col class="patent-name">
            <col class="patent-type">
            <col class="patent-supply-date">
            <col class="patent-public-date">
          </colgroup>
          <thead>
            <tr>
              <th>序号</th>
              <th>专利名称</th>
              <th>专利类型</th>
              <th>申请日</th>
              <th>公布时间</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for='(bizPatent, index) in bizPatentList' :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ bizPatent.Title | checkVariable }}</td>
              <td>{{ bizPatent.KindCodeDesc | checkVariable }}</td>
              <td>{{ bizPatent.ApplicationDate | checkVariable | formatTimeStamp}}</td>
              <td>{{ bizPatent.PublicationDate | checkVariable | formatTimeStamp}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-else class="no-data">
        暂无数据
      </div>
    </div>

  </div>
</template>
<script>
import {mapState} from 'vuex'
import Loading from '../Loading.vue'
export default {
  name: "",
  components: {
    'vLoading': Loading
  },
  data() {
    return {}
  },
  computed: {
    ...mapState ({
      bizTrademarkList: state => state.manage.bizTrademarkList,
      bizPatentList: state => state.manage.bizPatentList,
      bizTrademarkListLoading: state => state.manage.bizTrademarkListLoading,
      bizPatentListLoading: state => state.manage.bizPatentListLoading
    })
  }
}
</script>
<style lang="scss" scoped>
.logo-img {
  width: 116px;
  height: 45px;  
}
// 让商标往左边靠一些
.logo > tr > td:nth-child(2) {
  padding-left: 0px;
}

.logo-order {
  width: 100px;
}
.logo-pic {
  width: 250px;
}
.logo-name {
  width: 180px;
}
.logo-type {
  width: 140px;
}
.logo-status {
  width: 140px;
}

.patent-order {
  width: 70px;
}
.patent-name {
  width: 400px;
}
.patent-type {
  width: 110px;
}
.patent-supply-date {
  width: 110px;
}
.patent-public-date {
  width: 120px;
}

</style>




