<template>
  <div class="credit-paper-container">    
    <div id="page-content">
      <cover-special id="cover" class="module"></cover-special>      
      <v-menu class="module"></v-menu>
      <v-content1 class="module"></v-content1>
      <v-content2_3 class="module"></v-content2_3>
      <v-content4 class="module"></v-content4>
      <v-content5 class="module"></v-content5>
      
      <v-content7_9 class="module"></v-content7_9>
      <cover-end id="end" class="module"></cover-end>
    </div>
    <!-- <cover-base class="module"></cover-base> 暂不开放 -->
    <!-- <v-content6 class="module"></v-content6> 暂不开放 -->

    <!-- <div class="nav-button">
      <div @click="downloadPDF" class="download"  title="下载">
        <img src="../assets/download.png" alt="下载" >
      </div>
      <div class="jump-container">
        <div @click="jumpTop" class="jump-top">
          <a href="#cover">
            <img src="../assets/jump-top.png" alt="跳转到顶部">
          </a>
        </div>
        <div class="jump-bottom">
          <a href="#end">
            <img src="../assets/jump-bottom.png" alt="跳转到顶部">
          </a>
        </div>        
      </div>
    </div> -->
    
    
  </div>
</template>
<script>
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import ajax from '../utils/ajax.jsx'
import { mapMutations } from 'vuex'

import CoverSpecial from "@/components/creditpaper/CoverSpecial";
import CoverBase from "@/components/creditpaper/CoverBase";
import Menu from "@/components/creditpaper/Menu";
import Content1 from "@/components/creditpaper/Content1";
import Content2_3 from "@/components/creditpaper/Content2-3";
import Content4 from "@/components/creditpaper/Content4";
import Content5 from "@/components/creditpaper/Content5";
import Content6 from "@/components/creditpaper/Content6";
import Content7_9 from "@/components/creditpaper/Content7-9";
import CoverEnd from "@/components/creditpaper/CoverEnd";
export default {
  name: "CreditPaperContainer",
  components: {
    coverSpecial: CoverSpecial,
    coverBase: CoverBase,
    vMenu: Menu,
    vContent1: Content1,
    vContent2_3: Content2_3,
    vContent4: Content4,
    vContent5: Content5,
    vContent6: Content6,
    vContent7_9: Content7_9,
    CoverEnd: CoverEnd
  },
  data() {
    return {
      Tel: '',
      Email: '',
      searchKey: '',       //接收搜索页传来的查询参数
      download: false     //值为true时才会启动下载
    };
  },
  methods: {
    ...mapMutations([
      'updateBasicInfo',
      'updatePersonInfoList',
      'updatefiliationInfoList',
      'updateAlterInfoList',
      'updateEntinvInfoList',
      'updateCaseInfoList',
      'updateSharesImpawnInfoList',
      'updateMorDetailInfoList',
      // 'updatePunishBreakInfoList',
      'updateBizJusticeEquityList',
      'updateBizJudgmentDocList',
      'updateBizBlackList',
      'updateShareHolderInfoList',
      'updateBizLicenceList',
      'updateBizAnnualReportList',
      'updateBizTrademarkList',
      'updateBizPatentList',
      'updateBizOpException',
      'updateBizLogoutList',
      'updateBizUncreditExecutorList',
      'updateTel',
      'updateEmail',

      // 控制Loading
      'updateBizDetaiLoading',
      'updateBizJusticeEquityLoading',
      'updateBizJudgmentDocLoading',
      'updateBizBlackListLoading',
      'updateBizLicneceLoading',
      'updateBizAnnualReportListLoading',
      'updateBizTrademarkListLoading',
      'updateBizPatentListLoading',
      'updateBizOpExceptionListLoading',
      'updateBizLogoutListLoading',
      'updateBizUncreditExecutorLoading',
      'updateBizBriefInfoLoading'
    ]),
    doQueryBizBriefInfo(resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizBriefInfo',
        {
          search_key: vm.searchKey
        },
        function(res) {
          const data = res.Result
          vm.updateTel( data.Tel )
          vm.updateEmail( data.Email )
        }, function() {
          vm.updateBizBriefInfoLoading('no')
          resolve(true)
        }, 'get'
      )
    },
    doQueryUncreditExecutor(resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizUncreditExecutor',
        {
          search_key: vm.searchKey
        },
        function(res) {
          const data = res.Result
          vm.updateBizUncreditExecutorList( vm.returnList(data) )     //失信被执行人
        }, function() {
          vm.updateBizUncreditExecutorLoading('no')
          resolve(true)
        }, 'get'

      )
    },
    doQueryBizLogoutList(resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizLogout',
        {
          search_key: vm.searchKey
        },
        function (res) {
          const data = res.Result
          vm.updateBizLogoutList( vm.returnList(data) )   //企业简易注销公告信息
        }, function() {
          vm.updateBizLogoutListLoading('no')
          resolve(true)
        }, 'get'
      )
    },
    doQueryBizOpExceptionList(resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizOpException',
        {
          key_no: vm.searchKey
        },
        function (res) {
          const data = res.Result
          vm.updateBizOpException( vm.returnList(data) )     //异常经营名录
        }, function() {
          vm.updateBizOpExceptionListLoading('no')
          resolve(true)
        }, 'get'
      )

    },
    doQueryBizPatentList (resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizPatent',
        {
          search_key: vm.searchKey
        },
        function (res) {
          const data = res.Result
          vm.updateBizPatentList( vm.returnList(data) )   //专利信息
        }, function() {
          vm.updateBizPatentListLoading('no')
          resolve(true)
        }, 'get'
      ) 
    },
    doQueryBizTrademarkList (resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizTrademarkList',
        {
          search_key: vm.searchKey
        },
        function (res) {
          const data = res.Result
          vm.updateBizTrademarkList( vm.returnList(data) )    //商标
        }, function() {
          vm.updateBizTrademarkListLoading('no')
          resolve(true)
        }, 'get'
      )
    },
    doQueryBizAnnualReportList (resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizAnnualReport',
        {
          key_no: vm.searchKey
        },
        function(res) {
          const data = res.Result
          vm.updateBizAnnualReportList( vm.returnList(data) )   //企业年报

        }, function() {
          vm.updateBizAnnualReportListLoading('no')
          resolve(true)
        }, 'get'
      ) 
    },
    doQueryBizLicence (resolve) {
      const vm = this;
      vm.ajax(
        vm.apiHost + 'bizLicence',
        {
          search_key: vm.searchKey
        },
        function (res) {
          const data = res.Result
          console.log('doQueryBizLicence res: ', data)
          vm.updateBizLicenceList( vm.returnList(data) )      //行政许可信息
        }, function() {
          vm.updateBizLicneceLoading('no')
          resolve(true)
        }, 'get'
      )
    },
    doQueryBizBlackList (resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizBlackList',
        {
          search_key: vm.searchKey
        },
        function(res) {
          const data = res.Result
          vm.updateBizBlackList( vm.returnList(data) )      //黑名单
        }, function() {
          vm.updateBizBlackListLoading('no')
          resolve(true)
        }, 'get'
      )
    },
    doQueryBizJudgmentDoc (resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizJudgmentDoc',
        {
          search_key: vm.searchKey
        },
        function(res) {
          const data = res.Result
          vm.updateBizJudgmentDocList( vm.returnList(data) )  //裁判文书

        }, function() {
          vm.updateBizJudgmentDocLoading('no')
          resolve(true)
        }, 'get'
      ) 
    },
    doQueryBizJusticeEquity (resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizJusticeEquity',
        {
          search_key: vm.searchKey
        },
        function(res) {
          const data = res.Result    
          vm.updateBizJusticeEquityList( vm.returnList(data) )  //司法协助信息
          
        }, function() {
          vm.updateBizJusticeEquityLoading('no')
          resolve(true)
        }, 'get'
      )
    },
    // doQueryShareHolderInfoList () {
    //   const vm = this;
    //   vm.ajax (
    //     vm.apiHost + ''
    //   ) 
    // },
    doQueryBizAnnualReport () {
      // 还没通
      const vm = this
      // vm.ajax(
      //   vm.apiHost + 'bizAnnualReport',
      //   {
      //     key_no: ''
      //   },
      //   function (res) {

      //   }, null ,'get'
      // )
    },
    doQueryBizDetail (resolve) {
      const vm = this
      vm.ajax(
        vm.apiHost + 'bizDetail',
        {
          search_key: vm.searchKey
        },
        function(res) {
          console.log('doQueryBizDetail res: ', res)
          const data = res.Result
          vm.updateBasicInfo( data.BasicInfo )
          vm.updatePersonInfoList( vm.returnList(data.PersonInfoList) )         //高管信息
          vm.updatefiliationInfoList( vm.returnList(data.FiliationInfoList) )   //分支机构
          vm.updateAlterInfoList( vm.returnList(data.AlterInfoList) )           //变更记录
          vm.updateEntinvInfoList( vm.returnList(data.EntInvInfoList) )         //企业对外投资信息

          vm.updateShareHolderInfoList( vm.returnList(data.ShareHolderInfoList) ) //股东出资信息

          vm.updateCaseInfoList( vm.returnList(data.CaseInfoList) )               //行政处罚信息
          vm.updateSharesImpawnInfoList( vm.returnList(data.SharesImpawnInfoList) ) //股权出质
          vm.updateMorDetailInfoList( vm.returnList(data.MorDetailInfoList) )       //动产抵押登记信息
          
        }, function() {
          vm.updateBizDetaiLoading('no')          
          resolve(true)
        }, 'get'
      )    
    },
    downloadPDF () {
      console.log('download start.')
      const vm = this
      vm.htmlEle = document.querySelector('#downloadPaper');
      console.log('mounted html is : ', vm.htmlEle );
      vm.ajax(
        vm.apiHost + 'htmlToPdf',
        {
          htmlContent: vm.htmlEle.innerHTML
        },
        function(res) {
          console.log('htmlToPdf res is : ', res)
          
          window.open(vm.apiHost + 'download?pdfFilePath=' + res.pdfFilePath + '&key_no=' + vm.searchKey)

        }, null, 'post', null, null, null, 
        {
          'Content-Type': 'text/html; charset=utf-8;'
        }
      )
    }
    
  },
  created() { 
    const vm = this
    vm.searchKey = vm.GetQueryString('searchKey')
    vm.download = vm.GetQueryString('download')
    console.log('creditContainder download : ', vm.download)

    let p1 = new Promise(function(resolve, reject) {
      vm.doQueryBizDetail(resolve)
    })
    let p2 = new Promise(function(resolve, reject) {
      vm.doQueryBizJusticeEquity(resolve)
    })
    let p3 = new Promise(function(resolve, reject) {
      vm.doQueryBizJudgmentDoc(resolve)
    })
    let p4 = new Promise(function(resolve, reject) {
      vm.doQueryBizBlackList(resolve)
    })
    let p5 = new Promise(function(resolve, reject) {
      vm.doQueryBizLicence(resolve)
    })
    let p6 = new Promise(function(resolve, reject) {
      vm.doQueryBizAnnualReportList(resolve)
    })
    let p7 = new Promise(function(resolve, reject) {
      vm.doQueryBizTrademarkList (resolve)
    })
    let p8 = new Promise(function(resolve, reject) {
      vm.doQueryBizPatentList(resolve)
    })
    let p9 = new Promise(function(resolve, reject) {
      vm.doQueryBizOpExceptionList(resolve)
    })
    let p10 = new Promise(function(resolve, reject) {
      vm.doQueryBizLogoutList(resolve)
    })
    let p11 = new Promise(function(resolve, reject) {
      vm.doQueryUncreditExecutor(resolve)
    })
    let p12 = new Promise(function(resolve, reject) {
      vm.doQueryBizBriefInfo(resolve)
    })

    let pAll = Promise.all([p1,p2,p3,p4,p5,p6,p7,p8,p9,p10,p11,p12])
    pAll.then(function(resultList) {
      if (vm.download == 'yes') {        
        setTimeout(function() {
          vm.downloadPDF()
        }, 1000)
      }else {
        console.log('预览模式。')
      }     
    }).catch(function (resultList) {
      console.log("报错了： ", resultList)
    })
    

  }
};
</script>
<style lang="scss">
@import '../../static/CSS/variable.scss';
$main-color: #c02e34;
#page-content {
  // font-family: '幼圆',"Times New Roman", Times, serif;
}
.page-logo-container::after {
  content: " ";
  display: table;
  clear: both;
}
.page-logo {
  float: right;
  width: 70px;
  height: 18px;
  background-image: url("../assets/creditpaper/paper-logo.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

// 一级标题直接跟内容时，间隔
.chapter-container {
  margin-top: 30px;

  + .module-content {
    margin-top: 20px;
  }
}
// 内容与以及标题间隔
.module-content {
  + .chapter-container {
    margin-top: 30px;
  }
}
// 一级标题样式
.chapter-title {
  font-size: 36px;
  color: $main-color;
  letter-spacing: 0;
  line-height: 44px;
  text-align: left;
}
.chapter-line {
  margin-top: 12px;
  border-top-color: $main-color;
  border-top-style: solid;
  border-top-width: 1px;
}
// 二级标题样式
.module-title {
  font-size: 24px;
  color: #333333;
  text-align: left;
  line-height: 32px;
  margin-top: 21px;

  &::before {
    content: "";
    width: 2px;
    height: 14px;
    border-left-color: $main-color;
    border-left-style: solid;
    border-left-width: 3px;
    margin-right: 7px;
  }
}
// 二级标题与内容间隔
.module-title + .module-content {
  margin-top: 19px;
}
// 表头居中
.thead-center {
  > tr > th {
    text-align: center;
  }
}
// 表头居左
.thead-left {
  tr th {
    text-align: left;
  }
}
// 控制表格边框
.show-border {
  font-size: $contentFontSize;
  // font-size: 11px;
  border-collapse: collapse;
  border: 1px solid #dddddd;
  tr td {
    border-top: 1px solid #dddddd;
    // border: 1px solid #dddddd;
  }

  tr th {
    background-color: #eeeeee;
  }

  tr td,
  tr th {
    padding: 7px 0 7px 12px;    

    &:last-child {
      padding-right: 12px;
    }
  }
  tr td {
    text-align: left;
  }
}
.show-col-border {
  font-size: $contentFontSize;
  text-align: left;
  border-collapse: collapse;
  border: 1px solid #dddddd;

  tr td {
    border-left: 1px solid #dddddd;
  }

  tr .title {
    color: #333333;
    line-height: 18px;
    background-color: #dddddd;
  }
}
.show-col-border {
  text-align: left;
  tr th,
  tr td {
    vertical-align: middle;
    padding-right: 6px;
    &:nth-child(odd) {
      background-color: #eeeeee;
    }
    &:nth-child(even) {
      padding-right: 12px;
    }
  }
}
// 为合并单元格第一列提供右边栏
.show-merge-row-border {
  tr:nth-child(3n) {
    td:nth-child(1) {
      border-right: 1px solid #dddddd;
    }
  }
}
// 两个表格间隔
.table-margin + .table-margin {
  margin-top: 12px;
}
// 年度简单表通用样式
.common-simple-type {
  width: 140px;
}
.common-simple-year {
  width: 100px;
}
// 年度合并列通用表样式
.common-date {
  width: 100px;
}
.common-name {
  width: 200px;
}
.common-amount {
  width: 98px;
}
.common-rate {
  width: 60px;
}
.common-times {
  width: 80px;
}

// 表格总计行样式
.sum {
  td,
  th {
    background-color: #fafafa;
  }
}

.credit-paper-container {
  width: 100%;
  height: auto;
  // padding-top: 20px;
  // background: url("../assets/background-content.png") repeat-y 100% 100%;
}
.nav-button {
  position: fixed;
  display: inline-block;
  vertical-align: top;
  margin-left: 40px;

  @mixin icon-position ($top, $transY) {
    position: relative;
    width: 28px;
    height: 28px;
    top: $top;
    transform: translateY($transY);
  }

  .download {
    width: 60px;
    height: 60px;
    background: white;
    box-shadow: 0 0 4px 0;
    border-radius: 2px;
    cursor: pointer;

    img {
      @include icon-position(50%, -50%);
    }
  }
  .jump-container {
    margin-top: 20px;
    width: 60px;
    // height: 120px;
    color: black;
    background: white;

    .jump-top {
      height: 60px;
      cursor: pointer;
      img {
        @include icon-position(50%, -50%);
      }
    }
    .jump-bottom {
      height: 60px;
      cursor: pointer;
      img {
        @include icon-position(50%, -50%);
      }
    }
  }
}
</style>
<style lang="scss" scoped>

</style>




