<template>
  <div class="CoverEnd page-break-before">
    <div class="logo-container">
      <div class="logo"></div>
    </div>
    <div class="desc-container">
      <div class="title">中国小微企业征信档案库</div>
      <div class="sub-title">专注中小微的企业发展，规避企业风险，专注中小微的企业发展，规避企业风险，专注中小微的企业发展，规避企业风险，</div>
    </div>
    <div class="qrcode-container">
      <div class="qrcode">
        <img class="qrcode-logo" src="../../assets/qrcode-company.png" alt="二维码">
      </div>
      <div class="qrcode-desc">扫码关注百融金服公众号掌握更多数据</div>
    </div>
    <div class="footer-container">
      <span>创新</span>
      <span>合作</span>
      <span>共赢</span>          
    </div>    
  </div>
</template>
<script>
export default {
  name: "CoverEnd",
  data() {
    return {};
  }
};
</script>
<style lang="scss" scoped>
.CoverEnd {
  padding-top: 75px;
  width: 810px;
  height: 1100px;
}
.logo {
  width: 186px;
  height: 46px;
  margin: 0 auto;

  background-image: url("../../assets/creditpaper/end-logo.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.logo-container + .desc-container {
  margin-top: 62px;
}
.title {
  margin: 0 auto;
  font-size: 49px;
  line-height: 70px;
  color: #c02e34;
  text-align: center;

  + .sub-title {
    margin: 17px auto 0 auto;
  }
}

.sub-title {
  width: 415px;
  height: auto;

  font-size: 18px;
  color: #999999;
  text-align: center;
  line-height: 31px;
}
.qrcode-container {
  width: 100%;
  height: 481px;
  text-align: center;
  padding-top: 90px;
  margin-top: 60px;
  background-image: url("../../assets/creditpaper/end-background.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;

  .qrcode {
    width: 216px;
    height: 216px;
    margin: 0 auto;

    background-image: url("../../assets/creditpaper/qrcode-container.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;

    .qrcode-logo {
      position: relative;
      top: 11px;
      width: 193px;
      height: 193px;
    }

    + .qrcode-desc {
      margin-top: 10px;
    }
  }
  .qrcode-desc {
    width: 250px;
    margin: 0 auto;
    padding-top: 12px;
    font-size: 22px;
    color: #ffffff;
    line-height: 30px;
  }
}
.footer-container {
  margin: 88px auto 0 auto;
  font-size: 33px;
  color: #999999;
  line-height: 47px;
  text-align: center;

  span + span::before {
    content: '·';
    font-size: 32px;
    vertical-align: middle;
    margin-right: 10px;   
  }
}
</style>




