    <div class="module-content">
      <table class="show-border show-col-border table-margin equity-render">
        <colgroup>
          <col class="title-left">
          <col class="content-left">
          <col class="title-right">
          <col class="content-right">
        </colgroup>
        <tr>
          <td >登记证号</td><td >󞪒]10054号</td>
          <td >名称</td><td >可可西里</td>
        </tr>
        <tr>
          <td >种类</td><td >生活类</td>
          <td >出质人名称</td><td >李保国</td>
        </tr>
        <tr>
          <td >质权人名称</td><td >李保国</td>
          <td >质权登记期限</td><td >2016-12-09至2018-12-09</td>
        </tr>
        <tr>
          <td >状态</td><td >出质中</td>
          <td >公示日期</td><td >2016-12-09</td>
        </tr>
      </table>    
    </div> -->
