<script>
import { mapState } from 'vuex'
import Loading from '../Loading.vue'
export default {
  name: "",
  components: {
    'vLoading': Loading
  },
  computed: {
    ...mapState({
      caseInfoList: state => state.manage.caseInfoList,
      sharesImpawnInfoList: state => state.manage.sharesImpawnInfoList,
      morDetailInfoList: state => state.manage.morDetailInfoList,
      bizBlackList: state => state.manage.bizBlackList,
      bizOpExceptionList: state => state.manage.bizOpExceptionList,
      bizLogoutList: state => state.manage.bizLogoutList,
