  }
  .mortgage-date {
    width: 90px;
  }
  .mortgage-status {
    width: 90px;
  }

  .logout-company {
    width: 200px;
  }
  .logout-date-from {
    width: 115px;
  }
  .logout-date-to {
    width: 115px;
  }
  .logout-code {
