import Vue from 'vue'
import {mapState, mapMutations} from 'vuex'

import ajax from '@/utils/ajax.jsx'
import store from '@/store'
import * as filters from '../../../static/JS/filter.js' 
import methods from '../../../static/JS/methods.js'

import '../../../static/CSS/common.css'

import * as $ from 'jquery'


Vue.prototype.ajax = ajax
// 加载公共方法
Vue.use(methods)
// 加载将filters.js中的方法注册为全局过滤器
Object.keys(filters).forEach( key => {
  Vue.filter( key, filters[key])
} )

const apiConfig = {
  "env":"dev",
  "dev":{
    "apiHost":"/report/reportDetail/"
  },
  "qa":{
    "apiHost":"/report/reportDetail/"
  },
  "prod":{
    "apiHost":"/report/reportDetail/"
  }
}
const protocol = window.location.protocol + '//'
const host = window.location.host
const apiHost = protocol + host + apiConfig[apiConfig.env]['apiHost']
Vue.prototype.apiHost = apiHost


import CreditPaperContainer from "@/layout/CreditPaperContainer.vue";



var demo = new Vue({
  el: '#credit',
  store,
  components: {
    creditContainer: CreditPaperContainer
  },
  data: {
    htmlEle: ''
  },
  computed: {
    ...mapState({
      canDownLoad: state => state.manage.canDownLoad,
    })
  },
  methods: {
    ...mapMutations([
      'updateCanDownLoad'
    ]),
   
    

  }
 


})


