<template>
  <div class="page CoverBase">
    <div class="logo-container">
      <div class="logo"></div>
      <div class="logo-info ">详细了解企业信用尽在百融征信</div>
    </div>
    <div class="title-container total-block">
      <div class="title-sub-container">
        <div>
          <div class="title">
            企业信用报告 — 基础版
          </div>
          <div class="sub-title">
            本报告生成时间: 2018.2.28 15:24:34
          </div>
          <div class="sub-title">
            您所看到的报告内容为截止至该时间点该公司的百融征信数据快照
          </div>
        </div>        
      </div>
    </div>
    <div class="title-line"></div>
    <div class="bottom-title">
      <div class="bottom-title-square">
        <p>中国企业信用档案库尽在</p>
        <p>百融征信</p>
      </div>      
    </div>
  </div>
</template>
<script>
export default {
  name: "CoverBase",
  data() {
    return {};
  }
};
</script>
<style lang="scss" scoped>
.background-module {
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.clear {
  clear: both;
}
.CoverBase {
  padding-top: 76px;
}
.logo {
  float: left;
  margin-top: 15px;
  margin-left: 30px;
  width: 150px;
  height: 38px;
  @extend .background-module;
  background-image: url("../../assets/creditpaper/cover-special-logo.png");
}
.logo-info {
  float: right;
  margin-right: 30px;
  width: 192px;
  height: 66px;
  font-size: 24px;
  line-height: 33px;
  text-align: right;
  color: #999999;
  opacity: 0.5;
}
$titleWidth: 565px;
.title-container {
  margin-top: 140px;
  padding-top: 92px;
  width: $titleWidth;
  height: 290px;
  background-color: #cf3237;
}
.title-line {
  width: $titleWidth;
  height: 122px;
  background-color: #c02e34;
}
.title-sub-container {
  color: white;
  width: 444px;
  height: 196px;
  background-color: #de5847;

  div:first-child {
    text-align: left;
    padding: 48px 30px;
  }
}
.title {
  font-size: 36px;
  line-height: 50px;
}
.sub-title {
  font-size: 13px;
  line-height: 18px;
}
.title + .sub-title {
  margin-top: 10px;
}
.sub-title + .sub-title {
  margin-top: 4px;
}

.bottom-title {
  display: inline;
  font-size: 18px;
  color: #333333;
  
  text-align: center;
  
  // width: 250px;

}

.bottom-title-square {  
  margin: 77px auto 0 auto;
  p {
    line-height: 25px;
    letter-spacing: -.3px;
    margin: 0 0;
    &:nth-child(2) {
      color: #CF3237;
    }
  }

  // &::before {
  //   content: url('../../assets/creditpaper/border-left.png');
  //   position: relative;
  //   left: -110px;
  //   top: 60px;
  // }
  // &::after {
  //   content: url('../../assets/creditpaper/border-right.png');
  //   position: relative;
  //   right: -113px;
  //   top: -57px;
  // }

}


</style>




