<template>
  <div class="page-special page-content ">
    <!-- <div class="page-logo-container">
      <div class="page-logo"></div>
    </div> -->
    <div class="chapter-container">
      <div class="chapter-title">
        2 股东及出资信息
      </div>
      <div class="chapter-line"></div>
    </div>
    <div v-if="bizDetailLoading == 'yes'">
      <v-loading></v-loading>
    </div>
    <div v-else class="module-content">
      <div v-if="shareHolderInfoList.length > 0">
        <table class="shareholder show-border thead-left">
          <colgroup>
            <col class="shareholder-name">
            <col class="shareholder-amount">
            <col class="shareholder-type">
            <!-- <col class="shareholder-method"> -->
            <col class="shareholder-rate">
            <col class="shareholder-date">
          </colgroup>
          <thead>
            <tr>
              <th>股东</th>
              <th>认缴出资额（万元）</th>
              <th>认缴出资币种</th>
              <!-- <th>出资方式</th> -->
              <th>出资比例</th>
              <th>出资日期</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(holderInfo, index) in shareHolderInfoList" :key="index">
              <td>{{ holderInfo.ShaName | checkVariable }}</td>
              <td>{{ holderInfo.SubConAmt | checkVariable }}</td>
              <td>{{ holderInfo.RegCapCur | checkVariable }}</td>
              <!-- <td></td> -->
              <td>{{ holderInfo.FundedRatio | checkVariable }}</td>
              <td>{{ holderInfo.ConDate | checkVariable }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-else class="no-data">
        暂无数据
      </div>
    </div>
    <div class="chapter-container">
      <div class="chapter-title">
        3 对外投资
      </div>
      <div class="chapter-line"></div>
    </div>
    <div v-if="bizDetailLoading == 'yes'">
      <v-loading></v-loading>
    </div>
    <div v-else class="module-content">
      <table v-if="entinvInfoList.length > 0" class="investment show-border thead-left">
        <colgroup>
          <col class="investment-name">
          <col class="investment-date">
          <col class="investment-person">          
        </colgroup>
        <thead>
          <tr>
            <th>企业名称</th>
            <th>成立日期</th>
            <th>法人代表</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(entinv, index) in entinvInfoList" :key="index">
            <td>{{ entinv.EntName | checkVariable }}</td>
            <td>{{ entinv.EsDate | checkVariable }}</td>
            <td>{{ entinv.Name | checkVariable }}</td>
          </tr>       
        </tbody>
      </table>
      <div v-else class="no-data">
        暂无数据
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Loading from '../Loading.vue'
export default {
  name: "",
  components: {
    'vLoading': Loading
  },
  data() {
    return {}
  },
  computed: {
    ...mapState({
      entinvInfoList: state => state.manage.entinvInfoList,
      shareHolderInfoList: state => state.manage.shareHolderInfoList,
      bizDetailLoading: state => state.manage.bizDetailLoading
    })
  },
  created() {
    console.log('content2-3 entinvInfoList is : ', this.entinvInfoList)
  }
}
</script>
<style lang="scss" scoped>
.shareholder-name {
  width: 150px;
}
.shareholder-amount {
  width: 210px;
}
.shareholder-type {
  width: 150px;
}
.shareholder-rate {
  width: 150px;
}
.shareholder-date {
  width: 150px;
}

.investment-name {
  width: 350px;
}
.investment-date {
  width: 150px;
}
.investment-person {
  width: 310px;
}

</style>




